countries.forEach((country): void => {
  switch (country.continent) {
    case "NA":
    case "SA":
      americas.push(country);
      break;
    case "EU":
    case "AF":
      euNaf.push(country);
      break;
    case "AS":
    case "AU":
    case "AQ":
    default:
      asNau.push(country);
  }
});

export const numCountries = countries.length;

